body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #7cc6fe;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  font-family: "Verdana", cursive, sans-serif;
  color: #ffffff;
}

.card {
  background-color: #252d4a;
  max-width: 450px;
  min-height: 200px;
  height: min-content;
  border-radius: 15px;
  padding: 20px;
  margin: 10px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
}

.question-section {
  width: 100%;
  position: relative;
}

.question-count {
  width: 100%;
  height: 30px;
  margin-bottom: 20px;
  color: #fff;
  display: grid;
  grid-template-columns: 92% 8%;
}

.timer {
  border: 5px solid #234668;
  width: 20px;
  border-radius: 50%;
  text-align: center;
  padding: 4px;
  font-size: 16px;
  display: flex-end;
}

.answers-list {
  width: 100%;
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fill, minmax(170px, 1fr));
}

.result {
  width: 100%;
  font-size: 18px;
  text-align: center;
}

.btn-refresh {
  font-size: 16px;
  color: #ffffff;
  background-color: #252d4a;
  border-radius: 15px;
  display: flex;
  padding: 5px;
  align-items: center;
  border: 5px solid #234668;
  cursor: pointer;
  margin: auto;
  margin-top: 50px;
}

.btn {
  width: 100%;
  font-size: 16px;
  color: #ffffff;
  background-color: #252d4a;
  border-radius: 15px;
  display: flex;
  padding: 5px;
  justify-content: flex-start;
  align-items: center;
  border: 5px solid #234668;
  cursor: pointer;
}

button:hover {
  background-color: #555e7d;
}

button:focus {
  outline: none;
}

button svg {
  margin-right: 5px;
}
